export default {
	data() {
		return {
			loadStatus: "more",
			pageNum: 1,
			pageSize: 10,
			total: "",
			list: [],
			searchVal: "",
			timer: null,
			isEdit: false,
			staffList: [{
					name: "张三",
					img: "../../../../static/images/defaultUserPic.png",
					mobile: "13432221112",
					id: 1
				},
				{
					name: "李四",
					img: "../../../../static/images/defaultUserPic.png",
					mobile: "13432226222",
					id: 2
				},
				{
					name: "adsa",
					img: "../../../../static/images/defaultUserPic.png",
					mobile: "13432222262",
					id: 3
				},
				{
					name: "fasfas",
					img: "../../../../static/images/defaultUserPic.png",
					mobile: "13432222432",
					id: 4
				},
				{
					name: "aaaa",
					img: "../../../../static/images/defaultUserPic.png",
					mobile: "13432221222",
					id: 5
				}
			]
		}
	},
	onLoad() {
		this.clearList();
		this.getList(1);
	},
	methods: {
		reload() {
			this.clearList();
			this.getList(1);
		},
		getList(startPage) {
			const that = this;
			let param = {
				name: that.searchVal,
				startPage: startPage,
				pageSize: that.pageSize,
			}
			that.loadStatus = "loading";
			that.$api.employeeList(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
					that.total = res.datas.total;
					for (var i in result) {
						result[i].headImgUrl = result[i].headImgUrl ? result[i].headImgUrl :
							'../../../../static/images/defaultUserPic.png';
						that.list.push(result[i]);
					}
					let total = res.datas.total;
					let totalPageNum = Math.ceil(total / that.pageSize);
					if (parseInt(totalPageNum) > parseInt(that.pageNum)) {
						that.pageNum++;
						that.loadStatus = "more";
					} else {
						that.loadStatus = "noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		editBtn() {
			this.isEdit = !this.isEdit
		},
		constantlySearch(e) {
			const that = this;
			clearTimeout(that.timer);
			that.timer = setTimeout(function() {
				that.search();
			}, 800);
		},
		gotoDetail(id) {
			const that = this;
			that.$Router.push({
				path: "/pages/my/manageNavs/staff/detail",
				query: {
					id: id
				}
			})
		},
		gotoDistri(id) {
			const that = this;
			that.$Router.push({
				path: "/pages/my/manageNavs/staff/distribution",
				query: {
					id: id
				}
			})
		},
		czBtn(id, index) {
			const that = this;
			uni.showActionSheet({
				itemList: ['编辑员工资料', '删除员工'],
				success: (e) => {
					if (e.tapIndex == 0) {
						that.gotoEdit(id);
					} else if (e.tapIndex == 1) {
						uni.showModal({
							content: "确定要删除员工？",
							confirmText: "确认删除",
							cancelText: "取消",
							success: function(res) {
								if (res.confirm) {
									that.delEmployee(id, index);
									
								} else if (res.cancel) {

								}
							}
						});
					}
				}
			})
		},
		delEmployee(id,index) {
			const that = this;
			let param = {
				id:id
			}
			that.$api.employeeDelete(param).then(res => {
				if (res.success) {
					that.list.splice(index, 1);
					uni.showToast({
						title: "删除成功"
					})
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		gotoEdit(id) {
			this.$Router.push({
				path: "/pages/my/manageNavs/staff/edit",
				query:{
					id:id
				}
			})
		},
		gotoAdd() {
			this.$Router.push({
				path: "/pages/my/manageNavs/staff/add"
			})
		},
		clearList() {
			const that = this;
			that.pageNum = 1;
			that.total = "";
			that.list = [];
		},
		search() {
			const that = this;
			that.clearList();
			that.getList(1);
		},
		searchCancel() {
			const that = this;
			that.searchVal = "";
			that.clearList();
			that.getList(1);
			uni.hideKeyboard();
		},
	}
}
